
gridlayout {
    display: grid;
    max-height: 326px;
    overflow: scroll;
    justify-content: start;
    grid-template-rows: repeat(auto-fit, auto);
    text-wrap: nowrap;
    white-space: nowrap;
    padding-left: 10px;
  }

  gridlayout > * {
    padding: 10px;
    display: flex;
    align-items: center;
  }

  .first-col {
    position: relative; /* Add relative positioning to the position elements */
  }

  .first-col::before {
    opacity: 0.25;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 88vw;
    height: 1px; /* Adjust the height of the line as needed */
    background-image: linear-gradient(to right, rgba(52, 71, 103, 0), rgba(52, 71, 103, 0.4), rgba(52, 71, 103, 0));
  }
  .last-col {
    justify-content: center;
  }